html body{
    background-color: white;
    color: #242429;
    margin: 0;
  
}

/*NAV STYLING*/
nav ul li{
  
    list-style-type: none;
    display: inline-block; 
    font-size: 25px;
    padding-top: 10px;
    /* margin-left: auto;
    margin-right: auto; */
    /* border-radius: 10px; */
    /* box-shadow: 4px 4px 6px white; */
    /* justify-content: space-between; */
    /* position: fixed;
    top:0; */
    
}

nav{
    width: 100%;
    background-color: orange;
    position: fixed;
    top:0;
    z-index: 9999;
    box-shadow: 0 2px 2px  black;
    text-align: center;
}
nav ul li a{
    margin-right: 25px;
    color: #242429;
}

.activeL{
    color:rgb(0, 0, 204);
}

nav ul li a:hover{
    color:rgb(0, 0, 204);
    text-decoration: none;
    
}

nav.active{
    
    /* border-radius: 5px; */
    visibility: visible;
    transition: all 1.5s;
    

}
nav.hidden{

    visibility: hidden;
    transition: all 1.5s;    


}




